import { TextField } from "@mui/material";
import { Form, Formik, FormikHelpers } from "formik";
import React, { ChangeEvent, FC } from "react";
import { Button } from "../button";

import style from "./index.module.scss";
import { UploadImage } from "./uploadImage";
import { schema } from "./validateSchema";
import InputMask from "react-input-mask";
import { Position } from "../../../types/type";
import { PositionsRadioGroup } from "./positions-radio-group";
import axios from "axios";
import apiClient from "../../../common/apiClient";

type FormProps = {
  positions: Position[];
};

export type ValuesFormik = {
  name: string;
  email: string;
  phone: string;
  position: { id: number | null; name: string };
  image: string;
};

const initialValues: ValuesFormik = {
  name: "",
  email: "",
  phone: "",
  position: { id: null, name: "" },
  image: "",
};

export const PostForm: FC<FormProps> = ({ positions }) => {
  const onSubmit = async (
    values: ValuesFormik,
    formikHelpers: FormikHelpers<ValuesFormik>
  ) => {
    try {
      await apiClient.post(
        "/users",
        values
      );
    } catch (error) {
      console.log(error);
    } finally {
      formikHelpers.resetForm();
      // setImage
    }
  };

  return (
    <Formik
      initialValues={initialValues}
      onSubmit={onSubmit}
      validationSchema={schema}
    >
      {({
        values,
        errors,
        touched,
        handleChange,
        handleBlur,
        handleSubmit,
        isSubmitting,
        /* and other goodies */
        setFieldValue,
        dirty,
      }) => {
        const onChangePhoneNumber = (event: ChangeEvent<HTMLInputElement>) => {
          const value = event.target.value;
          setFieldValue("phone", value);
        };

        const onUploadImage = (value: string) => {
          setFieldValue("image", value);
        };

        const onChangePosition = (value: string, id: number) => {
          setFieldValue("position", { value, id });
        };
        const postUser = () => {};

        return (
          <Form className={style.wrapper} onSubmit={handleSubmit}>
            <TextField
              id="outlined-name"
              name="name"
              label="Your name"
              variant="outlined"
              color="secondary"
              className={style.form}
              value={values.name}
              onChange={handleChange}
              error={errors.name && touched.name ? true : false}
              helperText={errors.name && touched.name && errors.name}
            />
            <TextField
              id="outlined-email"
              label="Email"
              name="email"
              type="email"
              variant="outlined"
              color="secondary"
              className={style.form}
              value={values.email}
              onChange={handleChange}
              error={errors.email && touched.email ? true : false}
              helperText={errors.email && touched.email && errors.email}
            />
            <InputMask
              maskChar={null}
              mask="+38 (099) 999 99 99"
              onChange={onChangePhoneNumber}
            >
              {() => (
                <TextField
                  id="outlined-phone"
                  type="tel"
                  name="phone"
                  label="Phone"
                  placeholder="Phone"
                  variant="outlined"
                  color="secondary"
                  error={errors.phone && touched.phone ? true : false}
                  helperText={errors.phone && touched.phone && errors.phone}
                />
              )}
            </InputMask>

            <PositionsRadioGroup
              positions={positions}
              onChangePosition={onChangePosition}
            />
            <UploadImage
              image={values.image}
              onUploadImage={onUploadImage}
              error={errors.image && touched.image ? true : false}
            />
            <div className={style.buttonWrapper}>
              <Button color={dirty ? "primary" : "disabled"} type="submit">
                Sign up
              </Button>
            </div>
          </Form>
        );
      }}
    </Formik>
  );
};
