import querystring from "querystring";
import { useAsyncRetry } from "react-use";
import apiClient from "../common/apiClient";
import { UsersResponse } from "../types/type";

interface UseUsersParams {
  page: number;
  pageSize: number;
}

const fetchUsers = async (params: UseUsersParams) => {
  const { page, pageSize } = params;

  const queryParams = { page, count: pageSize };

  const query = querystring.stringify(queryParams);

  const url = `/users?${query}`;

  const response = await apiClient.get<UsersResponse>(url);

  const { success } = response.data;

  if (!success) throw new Error("Error while trying fetch users");

  return response.data;
};

export const useUsers = (params: UseUsersParams) => {
  const state = useAsyncRetry(async () => {
    const data = await fetchUsers(params);

    return data;
  }, [params.page]);

  return state;
};
