import React, { FC, useEffect, useMemo, useState } from "react";
import { Button } from "../button";
import { Card } from "../card";

import cn from "classnames";

import style from "./index.module.scss";
import { PAGE_SIZE } from "../../../constants";
import { Loader } from "../loader";
import usePagination from "../../../hooks/usePagination";
import { useUsers } from "../../../hooks/useUsers";

export const GetModule: FC = () => {
  const [totalPages, setTotalPages] = useState<null | number>(null);

  const pagination = usePagination({ initialPage: 1, totalPages });

  const [page] = pagination.pageState;

  const usersState = useUsers({ pageSize: PAGE_SIZE, page });

  const { value } = usersState;

  useEffect(() => {
    if (value?.total_pages) {
      setTotalPages(value?.total_pages);
    }
  }, [value?.total_pages]);

  const sortedUsers = useMemo(() => {
    const users = value?.users.sort(
      (a, b) => a.registration_timestamp - b.registration_timestamp
    );

    return users;
  }, [value?.users]);

  if (!value) return <Loader />;

  const { users } = value;

  const onShowMore = () => {
    pagination.next();
  };

  return (
    <div className={style.wrapper}>
      <div className={style.content}>
        <h1 className={style.title}>Working with GET request</h1>

        {
          <div className={style.cardsWrapper}>
            {sortedUsers?.map((user) => (
              <Card
                key={user.id}
                name={user.name}
                photo={user.photo}
                phone={user.phone}
                position={user.position}
                email={user.email}
              />
            ))}
          </div>
        }

        <div
          className={cn(style.buttonWrapper, {
            [style.invisible]: totalPages === page,
          })}
        >
          <Button
            color={!users.length ? "disabled" : "primary"}
            onClick={onShowMore}
          >
            Show more
          </Button>
        </div>
      </div>
    </div>
  );
};
